<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <el-badge :value="todos.length" class="item" v-if="todos.length>0">
        <span>Todos &nbsp;</span>
      </el-badge>
      <el-badge class="item" v-else>
        <span>Todos &nbsp;</span>
      </el-badge>
      <template>
        <el-checkbox-group id="version" v-model="checkList">
          <el-checkbox v-for="v in version" v-bind:key="v" v-bind:label="v"></el-checkbox>
        </el-checkbox-group>
      </template>
      <el-input v-model="inputTodo" placeholder="输入待办，Enter键增加" v-on:keyup.enter.native="addTodo">
        <el-button slot="append" @click="addTodo">添加</el-button>
      </el-input>
    </div>

    <div v-for="(todo,index) in todos" v-bind:key="todo.id" class="text item">
      <div class="todoList">
        <font>{{todo.text}}</font>
        <div style="width:100%;height:2px;"></div>
        <template>
          <el-checkbox-group v-model="todo.finishVersion" class="checkBoxDiv" @change="checkFinish($event,index)">
            <el-checkbox v-for="ve in todo.targetVersion" v-bind:key="ve" v-bind:label="ve"></el-checkbox>
          </el-checkbox-group>
        </template>
      </div>
    </div>

    <div class="block" v-if="!todos.length">
      <span class="demonstration"></span>
      <el-image :src="src"></el-image>
    </div>

  </el-card>
</template>

<script>
  export default {
    name: 'TodoItem',
    data() {
      return {
        version: ['cur', '708', '706', '705','704'],
        todos: [],
        inputTodo: '',
        checkList: ['cur'],
        src: require('../assets/todo.jpg')
      }
    },
    methods: {
      addTodo: function () {
        if (!this.inputTodo.trim()) {
          return this.$message({
            message: '请输入待办任务。',
            type: 'warning'
          });
        }
        this.todos.push({
          id: this.newGuid(),
          text: this.inputTodo,
          targetVersion: this.checkList,
          finishVersion: []
        });
        this.inputTodo = '';
        window.localStorage['todos'] = JSON.stringify(this.todos);
      },
      checkFinish: function ($event, index) {
        //this.$message('这是一条消息提示');
        const targetVersionItem = this.todos[index].targetVersion;
        if ($event.length == targetVersionItem.length) {
          this.$confirm('所有版本都改完了，删任务不?', '提示', {
            confirmButtonText: '删 呀 ！！！',
            cancelButtonText: '点错了抱歉',
            type: 'warning'
          }).then(() => {
            this.todos.splice(index, 1);
            this.$message({
              type: 'success',
              message: '优秀!'
            });
            window.localStorage['todos'] = JSON.stringify(this.todos);
          }).catch(() => {
            window.localStorage['todos'] = JSON.stringify(this.todos);
          });
        }
        else{
          window.localStorage['todos'] = JSON.stringify(this.todos);
        }
      },
      newGuid: function () {
        var S4 = function () {
          return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
        };
        return (S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4());
      }
    },
    created: function () {
      this.todos = window.localStorage['todos'] ? JSON.parse(window.localStorage['todos']) : [];
    }
  }
</script>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 10px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }

  #version {
    margin-bottom: 10px;
  }

  .todoList {
    border-bottom: 1px solid #EBEEF5;
    padding: 5px 10px;
  }
  .todoList font{
    font-size: 15px;
  }
  .checkBoxDiv{
    margin-top: 5px;
  }
</style>